import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
import kmLi from '../../components/km-li.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	data() {
		return {
			userType:0,		//0表示货主，1表示车主
			carNum:'1',
			type: '',
			isAgree:'',
			status:1,	//1:待确认；2:已确认；
			lookInfoNeedMoney:100,
			cutMoney:0,
			uploadPhoenStep:1,	//上传过磅照片步骤，1：上传出厂过磅照片；2：上传入厂过磅照片；3：订单等待确认
			uploadPhoto:'',
			uploadSuccess:0,
			outPhoto:'',
			inPhoto:'',
			orderId:0,
			url:'',
			order:{}
		}
	},
	components: {
		uniNumberBox,
		kmLi,
		uniPopup
	},
	onLoad(e) {
		var _this = this;
		this.orderId = e.id;
		/* this.status = e.status; */
		this.url = this.serverURL;
		uni.removeStorage({
			key:'cutMoney',
			success() {
				
			}
		});
		
		uni.request({
			url: _this.url + 'home/user/user_order_id',
			method:'POST',
			data:{
				id: _this.orderId
			},
			success(res) {
				console.log(res);
				if(res.data.code==100){
					var order = res.data.msg;
					_this.order = order;
					_this.status = order.states;
				}
			},
			fail(error) {
				console.log(error)
			}
		})
		
		
	},
	onShow() {
		var _this = this;
		uni.getStorage({
			key:'cutMoney',
			success(res) {
				console.log(res.data);
				_this.cutMoney = res.data * (-1);
			}
		})
	},
	methods: {
		changeCar(value) {
			this.carNum = value;
		},
		togglePopup(type) {
			this.type = type;
		},
		checkboxChange(e) {
			if(e.detail.value[0]=="agree"){
				this.isAgree = e.detail.value[0];
			} else {
				this.isAgree ='';
			}
		},
		isSure() {
			this.status = 2;
			if(this.isAgree == "agree"){
				console.log("agree");
			}
		},
		selectYhq() {
			uni.navigateTo({
				url:'../selectCoupon/selectCoupon'
			})
		},
		nextStep(step) {
			var _this = this;
			if(_this.uploadSuccess==1){
				_this.uploadPhoenStep = step;
				_this.uploadSuccess = 0;
				_this.uploadPhoto = '';
			}
		},
		uploadImg() {
			var _this = this;
			uni.chooseImage({
				count:1,
				success(res) {
					var imgPath = res.tempFilePaths[0];
					_this.uploadPhoto = imgPath;
					_this.uploadSuccess = 1;
					if(_this.uploadPhoenStep==1){
						_this.outPhoto = imgPath;
					} else if(_this.uploadPhoenStep==2){
						_this.inPhoto = imgPath;
					}
					console.log("选择图片成功");
				},
				fail() {
					console.log("选择图片失败");
				},
				complete() {
					console.log("选择图片完成");
				}
			})
		},
		confirmOrder() {
			uni.showLoading({
				title:'确认中...',
				mask:true
			})
			var _this = this;
			uni.request({
				url: _this.url + 'home/Order/update_transportation_order',
				method:'POST',
				data:{
					id: _this.orderId,
					states: 7
				},
				success(res) {
					console.log(res);
					uni.hideLoading();
					if(res.data.code==100){
						_this.status = 7;
						uni.showToast({
							title:'确认成功',
							icon:"success",
							mask:true
						})
					}
				},
				fail(error) {
					console.log(error)
				}
			})
		}
	}
}
